import { connect } from 'react-redux'
import InactiveMemberPricingSection from 'Employer/components/InactiveMemberPricingSection'
import { neverHadAnyMembership } from 'Shared/selectors/membership'
import { fetchCurrentMembershipIfNeeded } from 'Shared/ducks/membership'

const mapStateToProps = state => ({
  neverHadAnyMembership: neverHadAnyMembership(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentMembership: () => dispatch(fetchCurrentMembershipIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(InactiveMemberPricingSection)



