import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Link, withRouter } from 'react-router-dom'
import { SmallWithIconButton, SmallJustIconButton } from 'Shared/components/Button'
import DropdownMenu from 'Shared/components/DropdownMenu'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import withTranslations from 'Shared/hoc/withTranslations'
import { generateJobOfferURL } from 'Shared/helpers/string'
import ShareDropdown from 'Shared/components/ShareDropdown'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'
import mixpanel from 'mixpanel-browser'

class ActiveOfferButtons extends Component {
  static propTypes = {
    data: PropTypes.object.isRequired,
    setPrivateToggle: PropTypes.func.isRequired,
    handleDeactivate: PropTypes.func.isRequired
  }

  static defaultProps = {
    data: {
      title: '',
      city: '',
      organisation: {
        name: ''
      }
    }
  }

  state = {
    showConfirmDeactivateModal: false
  }

  openConfirmDeactivateModal = () => this.setState({ showConfirmDeactivateModal: true })
  hideConfirmDeactivateModal = () => this.setState({ showConfirmDeactivateModal: false })

  render () {
    const { t, data, handleDeactivate, organisation } = this.props
    const menuOptions = {
      triggerButton: <SmallJustIconButton><i className='ion-ios-more' /></SmallJustIconButton>,
      align: 'right',
      closeOnOutsideClick: true,
      closeOnInsideClick: false
    }

    return (
      <div className='job-offer-card__active-buttons'>
        <Link to={`/employer/job-offer/${data.id}/candidates`} onClick={() => mixpanel.track("Button Click - Find Candidates - Employer Dashboard")}>
          <SmallWithIconButton><i
            className='icon-im-members' />{t('employer.jobOffers.findCandidates')}
          </SmallWithIconButton>
        </Link>
        <ShareDropdown
          shareUrl={generateJobOfferURL(data)}
          title={`${data.title} at ${organisation.name} in ${data.city}`}
        />
        <DropdownMenu {...menuOptions}>
          <li className='job-offer-cards__dropdown-list-item'>
            <Link to={{
              pathname: generateJobOfferURL(data),
              state: { from: this.props.history.location.pathname }
            }}
            >
              <div className='job-offer-card__dropdown-link'>
                <i className='ion-md-search' />
                <span className='job-offer-card__dropdown-link-text'>{t('common.preview')}</span>
              </div>
            </Link>
          </li>
          <li className='job-offer-cards__dropdown-list-item'>
            <LimitedAccessWrapperConnector
              placeholderComponent={<div className='job-offer-card__dropdown-link'>
                <i className='ion-md-create' />
                <span className='job-offer-card__dropdown-link-text'>{t('common.edit')}</span>
              </div>}
              allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
              jobOfferId={data.id}
            >
              <Link to={`/employer/job-offer/${data.id}/edit`}>
                <div className='job-offer-card__dropdown-link'>
                  <i className='ion-md-create' />
                  <span className='job-offer-card__dropdown-link-text'>{t('common.edit')}</span>
                </div>
              </Link>
            </LimitedAccessWrapperConnector>
          </li>
          <li className='job-offer-cards__dropdown-list-item'>
            <LimitedAccessWrapperConnector
              placeholderComponent={<div
                className='job-offer-card__dropdown-link job-offer-cards__dropdown-link--deactivate'>
                <i className='ion-md-power' />
                <span
                  className='job-offer-card__dropdown-link-text'>{t('employer.jobOffers.deactivate')}</span>
              </div>}
              allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
              jobOfferId={data.id}
            >
              <div
                className='job-offer-card__dropdown-link job-offer-cards__dropdown-link--deactivate'
                onClick={this.openConfirmDeactivateModal}>
                <i className='ion-md-power' />
                <span
                  className='job-offer-card__dropdown-link-text'>{t('employer.jobOffers.deactivate')}</span>
              </div>
            </LimitedAccessWrapperConnector>
          </li>
        </DropdownMenu>
        <ConfirmModal
          title={t('modals.deactivateJobOffer.title')}
          content={t('modals.deactivateJobOffer.description', { jobTitle: data.title })}
          handleConfirm={handleDeactivate}
          submitText={t('common.yes')}
          isModalVisible={this.state.showConfirmDeactivateModal}
          hideConfirmModal={this.hideConfirmDeactivateModal}
        />
      </div>
    )
  }
}

export default withRouter(withTranslations(ActiveOfferButtons))



