import React, { Component, Fragment } from 'react'
import UserAvatar from 'Shared/components/UserAvatar'
import { ConfirmPopup } from 'Shared/components/Popup'
import { BigJustIconButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import './TeamMembersList.scss'
import * as R from 'ramda'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

class TeamMembersList extends Component {
  componentDidMount () {
    this.props.getOrganisationEmployers()
  }

  showMembersAvatarsList = members => members.map((member) => {
    const Button = <div className='members-list__avatar-conatiner'>
      <UserAvatar
        className='members-list__avatar'
        details={{ firstName: member.firstName, lastName: member.lastName, photoUrl: member.photoUrl, avatarColour: member.avatarColour }}
      />
    </div>

    return <div className='members-list__member' key={`list-${member.id}`}>
      <ConfirmPopup
        position='bottom'
        align='center'
        withSubmit={false}
        withCancel={false}
        triggerButton={Button}
      >
        <div className='members-list__popup'>
          <span>{member.firstName} {member.lastName}</span>
        </div>
      </ConfirmPopup>
    </div>
  })

  showMoreMembers = members => {
    const Button = <div className='members-list__show-more'>+{members.length}</div>

    return <div className='members-list__member members-list__member--show-more'>
      <ConfirmPopup
        position='bottom'
        align='center'
        withSubmit={false}
        withCancel={false}
        triggerButton={Button}
      >
        <div className='members-list__popup'>
          {this.showMoreMembersList(members)}
        </div>
      </ConfirmPopup>
    </div>
  }

  showMoreMembersList = members => members.map(member => (
    <div key={`show-more-${member.id}`} className='members-list__show-more-item'>
      <UserAvatar
        details={{ firstName: member.firstName, lastName: member.lastName, photoUrl: member.photoUrl, avatarColour: member.avatarColour }}
      />
      <div className='members-list__show-more-name'>{member.firstName} {member.lastName}</div>
    </div>
  ))

  get membersList () {
    const { members } = this.props
    if (members.length <= 3) return this.showMembersAvatarsList(members)
    if (members.length > 3) {
      return <Fragment>
        {this.showMembersAvatarsList(R.take(3, members))}
        {this.showMoreMembers(R.takeLast(members.length - 3, members))}
      </Fragment>
    }

    return null
  }

  redirectToMembersSettings = () => this.props.history.push('/employer/settings/team-members')

  render () {
    const { isAdminEmployer, t } = this.props

    return (
      <div className='team-members-list'>
        {this.membersList}
        {
          isAdminEmployer && (
            <div className='members-list__add-member-button'>
              <Tooltip trigger={'mouseenter'} content={t('employer.jobOffers.InviteTeamMembers')} style={{'display': 'block'}}>
                <BigJustIconButton onClick={this.redirectToMembersSettings}><i className='icon-im-members' /></BigJustIconButton>
              </Tooltip>
            </div>
          )
        }
      </div>
    )
  }
}

export default withTranslations(TeamMembersList)



