import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import JobOffersViewWeb from './JobOffersView'
import JobOffersViewMobile from './JobOffersView.mobile'

const JobOffersView = props => props.isMobile ? <JobOffersViewMobile {...props} /> : <JobOffersViewWeb {...props} />

export default withDeviceWidthDetector(JobOffersView)



