import React from 'react'
import { connect } from 'react-redux'
import { t } from 'i18next'
import { initOrganisationJobOffersByStatusIfNeeded, handleHidePostJobOfferAlert } from 'Shared/ducks/jobOffers'
import JobOffersList from 'Shared/components/JobOffersList/JobOffersList'
import { getSortedDeactivatedJobOffers, getPublishedJobOffersIds } from 'Shared/selectors/jobOffers'
import { Statuses } from 'Shared/constants/Job'
import ApplicantsCounters from 'Shared/components/JobOfferListElement/components/ApplicantsCounters'
import { getApplicationsCountersByTypeAndId } from 'Shared/selectors/jobOfferApplications'
import { getApplicationsCounters } from 'Shared/ducks/jobOfferApplications'
import DeactivatedOfferButtons from 'Shared/components/JobOfferListElement/components/DeactivatedOfferButtons'
import { getOrganisationName } from 'Shared/selectors/organisations'

const jobOfferListCategory = Statuses.inactive

const mapStateToProps = state => {
  const organisationId = state.user.details.organisationId

  return {
    jobOfferPublishedIds: getPublishedJobOffersIds(state),
    jobOfferListCategory,
    postAJobAccessAlertModalVisible: state.jobOffers.postJobOfferAlertVisible,
    jobOffersList: getSortedDeactivatedJobOffers(state.user.details.organisationId)(state),
    jobOfferCard: {
      body: jobOfferData => <ApplicantsCounters jobOfferListCategory={jobOfferListCategory} counters={[
        {
          link: `/employer/job-offer/${jobOfferData.id}/applicants/undecided`,
          amount: getApplicationsCountersByTypeAndId(jobOfferData.id, 'undecided')(state),
          name: t('employer.jobOffers.counters.applicants')
        },
        {
          link: `/employer/job-offer/${jobOfferData.id}/applicants/shortlisted`,
          amount: getApplicationsCountersByTypeAndId(jobOfferData.id, 'shortlisted')(state),
          name: t('employer.jobOffers.counters.shortlisted')
        },
        {
          link: `/employer/job-offer/${jobOfferData.id}/applicants/contacted`,
          amount: getApplicationsCountersByTypeAndId(jobOfferData.id, 'contacted')(state),
          name: t('employer.jobOffers.counters.contacted')
        },
        {
          link: `/employer/job-offer/${jobOfferData.id}/applicants/interviewed`,
          amount: getApplicationsCountersByTypeAndId(jobOfferData.id, 'interviewed')(state),
          name: t('employer.jobOffers.counters.interviewed')
        },
        {
          link: `/employer/job-offer/${jobOfferData.id}/applicants/hired`,
          amount: getApplicationsCountersByTypeAndId(jobOfferData.id, 'hired')(state),
          name: t('employer.jobOffers.counters.hired')
        }
      ]} />,
      buttons: jobOfferData => <DeactivatedOfferButtons data={jobOfferData} />
    },
    companyName: getOrganisationName(state, organisationId)
  }
}

const mapDispatchToProps = dispatch => ({
  loadJobOffers: () => dispatch(initOrganisationJobOffersByStatusIfNeeded(jobOfferListCategory)),
  getApplicationsCounters: (jobOffersIds) => dispatch(getApplicationsCounters(jobOffersIds)),
  hidePostAJobAccessAlertModal: () => dispatch(handleHidePostJobOfferAlert())
})

export default connect(mapStateToProps, mapDispatchToProps)(JobOffersList)



