import React, { Component } from 'react'
import { Switch, Route, Redirect, withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { PrimaryButton } from 'Shared/components/Button'
import withTranslation from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import { EntitiesListTabWrapper, EntitiesListTabLink } from 'Shared/components/EntitiesListTabs'
import DraftJobOffersListConnector from 'Employer/connectors/JobOffers/DraftJobOffersListConnector'
import ActiveJobOffersListConnector from 'Employer/connectors/JobOffers/ActiveJobOffersListConnector'
import DeactivatedJobOffersListConnector from 'Employer/connectors/JobOffers/DeactivatedJobOffersListConnector'
import { isMobile } from 'Shared/helpers/mobile'
import InactiveMemberPricingSectionConnector from 'Employer/connectors/Components/InactiveMemberPricingSectionConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import TeamMembersListConnector from 'Employer/connectors/TeamMembers/TeamMembersListConnector'
import './JobOfferView.scss'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import OliviaBotBannerConnector from 'Employer/connectors/OliviaBotBanner/OliviaBotBannerConnector'
import { EmployerPermissions } from 'Shared/constants/User'
import Modal from 'Shared/layouts/CareerPro/Components/Modal/Modal'
import { removeLocalData, getLocalData } from 'Shared/helpers/session'
import mixpanel from 'mixpanel-browser'
class JobOffersView extends Component {
  static propTypes = {
    createNewEmptyJob: PropTypes.func.isRequired,
    fetchOrganisationJobOffersCounts: PropTypes.func.isRequired,
    count: PropTypes.object.isRequired,
    creatingNewJobOffer: false
  }

  state = {
    currentStatus: this.props.location.pathname.replace(/(\/employer\/job-offers\/)(\w+)/i, '$2'),
    modalIsOpen: getLocalData('new_company_signup') || false
  }

  componentDidMount () {
    this.props.fetchOrganisationJobOffersCounts()
    mixpanel.track("Page View - Job Offers - Employer")
  }

  handleStatusSelect = e => {
    this.setState({ currentStatus: e.target.value })
    return this.props.history.push(`/employer/job-offers/${e.target.value}`)
  }

  handlePostJob = async () => {
    this.setState({ creatingNewJobOffer: true })
    await this.props.createNewEmptyJob()
    return this.setState({ creatingNewJobOffer: false })
  }

  get statusOptions () {
    const { t, count } = this.props

    const statusList = [
      {
        name: t('employer.jobOffers.jobOffersType.active'),
        value: t('employer.jobOffers.jobOffersType.active').toLowerCase(),
        count: count.active
      },
      {
        name: t('employer.jobOffers.jobOffersType.drafts'),
        value: t('employer.jobOffers.jobOffersType.draft').toLowerCase(),
        count: count.draft
      },
      {
        name: t('employer.jobOffers.jobOffersType.inactive'),
        value: t('employer.jobOffers.jobOffersType.inactive').toLowerCase(),
        count: count.inactive
      }
    ]

    return statusList.map(status => <option key={`job-status-${status.value}`} value={status.value}>
      {`${status.name} (${status.count})`}
    </option>)
  }

  closeModal = () => {
    this.setState({modalIsOpen: false})
    removeLocalData('new_company_signup')
  }

  render () {
    const { count, t } = this.props
    return (
      <section className='job-offers-view__container parsing--hidden'>
        <Modal
          title=' '
          modalIsOpen={this.state.modalIsOpen}
          hideCloseBtn={false}
          closeModal={this.closeModal}
          status='success'
          heading={t('modals.newCompanySignUp.title')}
          des={t('modals.newCompanySignUp.description')}
          textCenter={true}
        />

        <PageTopHeaderMobile title={t('common.jobs')} />
        <Container>
          <InactiveMemberPricingSectionConnector />
          <OliviaBotBannerConnector className='job-offers-view__olivia-bot' />
          <div className='job-offer-view__heading'>
            <h1 className='job-offer-view__header'>{t('employer.jobOffers.heading')}</h1>
            <div className='job-offer-view__heading-buttons'>
              <TeamMembersListConnector />
              <LimitedAccessWrapperConnector
                placeholderComponent={<PrimaryButton
                  className='job-offer-view__add-button'>
                  <i className='ion-md-add job-offer-view__add-button-icon' />
                  <span className='job-offer-view__add-button-text'>{t('common.postJob')}</span>
                </PrimaryButton>}
                allowedFor={[EmployerPermissions.jobPosts]}
              >
                <PrimaryButton
                  disabled={this.state.creatingNewJobOffer}
                  className='job-offer-view__add-button'
                  onClick={this.handlePostJob}>
                  <i className='ion-md-add job-offer-view__add-button-icon' />
                  <span className='job-offer-view__add-button-text'>{t('common.postJob')}</span>
                </PrimaryButton>
              </LimitedAccessWrapperConnector>
            </div>
          </div>
          <div className='job-offer-view__tabs'>
            <EntitiesListTabWrapper>
              <EntitiesListTabLink
                to='/employer/job-offers/active'
                text={t('employer.jobOffers.jobOffersType.active')}
                badge={count.active}
              />
              <EntitiesListTabLink
                to='/employer/job-offers/draft'
                text={t('employer.jobOffers.jobOffersType.drafts')}
                badge={count.draft} />
              <EntitiesListTabLink
                to='/employer/job-offers/deactivated'
                text={t('employer.jobOffers.jobOffersType.inactive')}
                badge={count.inactive}
              />
            </EntitiesListTabWrapper>
          </div>
          {
            isMobile() &&
            <div className='job-offer-view__select--mobile'>
              <div className={'form__group form__group--select-with-icons'}>
                <select id='responsive-step-menu' onChange={this.handleStatusSelect} className='form__input' value={this.state.currentStatus}>
                  {this.statusOptions}
                </select>
                <i className={'form__input__icon form__input__icon--vanilla-select icon-im-caret-down'} />
              </div>
            </div>
          }
          <div className='job-offer-view'>
            <Switch>
              <Route
                path='/employer/job-offers/active'
                component={ActiveJobOffersListConnector}
              />
              <Route path='/employer/job-offers/draft' component={DraftJobOffersListConnector} />
              <Route path='/employer/job-offers/deactivated' component={DeactivatedJobOffersListConnector} />
              <Redirect from='/employer/job-offers' to='/employer/job-offers/active' />
            </Switch>
          </div>
        </Container>
      </section>
    )
  }
}

export default withRouter(withTranslation(JobOffersView))



