import React from 'react'
import PropTypes from 'prop-types'
import { Link, withRouter } from 'react-router-dom'
import { SmallJustIconButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'

const DeactivatedOfferButtons = ({ t, data, history }) => {
  return (
    <div className='job-offer-card__draft-buttons'>
      <LimitedAccessWrapperConnector
        placeholderComponent={<div className='job-offer-list-element__button-link-placeholder'>
          <SmallJustIconButton><i
            className='ion-md-create' />
          </SmallJustIconButton>
        </div>}
        allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
        jobOfferId={data.id}
      >
        <Link className='job-offer-list-element__button-link' to={`/employer/job-offer/${data.id}/edit`}>
          <SmallJustIconButton><i
            className='ion-md-create' />
          </SmallJustIconButton>
        </Link>
      </LimitedAccessWrapperConnector>
    </div>
  )
}

DeactivatedOfferButtons.propTypes = {
  data: PropTypes.object.isRequired
}

export default withRouter(withTranslations(DeactivatedOfferButtons))



