import { connect } from 'react-redux'
import ActiveOfferButtons from 'Shared/components/JobOfferListElement/components/ActiveOfferButtons'
import { getUsersOrganisation } from 'Shared/selectors/organisations'
import { updateJobOfferById } from 'Shared/ducks/jobOffers'
import { Statuses } from 'Shared/constants/Job'

const mapStateToProps = state => ({
  organisation: getUsersOrganisation(state)
})

const mapDispatchToProps = (dispatch, ownProps) => {
  const { data: { id } } = ownProps

  return ({
    handleDeactivate: () => dispatch(updateJobOfferById(id, { status: Statuses.inactive }))
  })
}

export default connect(mapStateToProps, mapDispatchToProps)(ActiveOfferButtons)



