import React from 'react'
import { connect } from 'react-redux'
import { t } from 'i18next'
import { initOrganisationJobOffersByStatusIfNeeded, handleHidePostJobOfferAlert } from 'Shared/ducks/jobOffers'
import JobOffersList from 'Shared/components/JobOffersList/JobOffersList'
import { getEmployersJobOffersByStatus } from 'Shared/selectors/jobOffers'
import { Statuses } from 'Shared/constants/Job'
import Date from 'Shared/components/JobOfferListElement/components/Date'
import DraftOfferButtonsConnector from 'Employer/connectors/JobOffers/DraftOfferButtonsConnector'
import { getEmployerCompanyDetailsCompletion, getOrganisationName } from 'Shared/selectors/organisations'
import { LIST_ITEMS_PER_PAGE } from 'Shared/constants/Global'

const jobOfferListCategory = Statuses.draft

const mapStateToProps = state => {
  const organisationId = state.user.details.organisationId

  return {
    isCompanyProfileComplete: getEmployerCompanyDetailsCompletion(state),
    jobOfferListCategory,
    perPage: LIST_ITEMS_PER_PAGE,
    postAJobAccessAlertModalVisible: state.jobOffers.postJobOfferAlertVisible,
    jobOffersList: getEmployersJobOffersByStatus(state, state.user.details.organisationId, jobOfferListCategory),
    jobOfferCard: {
      body: jobOfferData => <Date date={jobOfferData.updatedAt} dateType={t('common.updated')} />,
      buttons: jobOfferData => <DraftOfferButtonsConnector data={jobOfferData} />
    },
    companyName: getOrganisationName(state, organisationId)
  }
}

const mapDispatchToProps = dispatch => ({
  loadJobOffers: () => dispatch(initOrganisationJobOffersByStatusIfNeeded(jobOfferListCategory)),
  hidePostAJobAccessAlertModal: () => dispatch(handleHidePostJobOfferAlert())
})

export default connect(mapStateToProps, mapDispatchToProps)(JobOffersList)



