import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { PrimaryLightBorderedButton } from 'Shared/components/Button'
import './InactiveMemberPricingSection.scss'
import InactiveMemberPricingImage from 'Shared/assets/images/illustrations/inactivePricingSection.svg'
import withTranslations from 'Shared/hoc/withTranslations'

class InactiveMemberPricingSection extends Component {
  componentDidMount () {
    this.props.fetchCurrentMembership()
  }

  render () {
    const { t, neverHadAnyMembership } = this.props
    return neverHadAnyMembership && (
      <div className='inactive-pricing-section'>
        <div className='columns'>
          <div className='column col-9 col-xs-12'>
            <div className='inactive-pricing-section__content'>
              <div className='inactive-pricing-section__heading'>{t('employer.inactiveMembershipPricing.title')}</div>
              <div className='inactive-pricing-section__description'>{t('employer.inactiveMembershipPricing.description')}</div>
              <div className='inactive-pricing-section__cta'>
                <Link to='/pricing'>
                  <PrimaryLightBorderedButton>
                    {t('employer.inactiveMembershipPricing.cta')}
                  </PrimaryLightBorderedButton>
                </Link>
              </div>
            </div>
          </div>
          <div className='column col-3 col-xs-12'>
            <div className='inactive-pricing-section__image'>
              <img src={InactiveMemberPricingImage} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(InactiveMemberPricingSection)



