import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import TeamMembersList from 'Employer/components/TeamMembersList'
import { getOrganisationEmployers } from 'Shared/ducks/employers'
import { getAllOrganisationEmployersExceptSelf } from 'Shared/selectors/employers'
import { isAdminEmployer } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  members: getAllOrganisationEmployersExceptSelf(state, state.user.details.organisationId),
  isAdminEmployer: isAdminEmployer(state)
})

const mapDispatchToProps = dispatch => ({
  getOrganisationEmployers: () => dispatch(getOrganisationEmployers())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TeamMembersList))



