import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import cx from 'classnames'
import { Statuses } from 'Shared/constants/Job'

const ApplicantsCounters = props => {
  const counterStyle = cx({
    'column': true,
    'job-offer-card__counter-container': true,
    'col-2': props.jobOfferListCategory === Statuses.inactive,
    'col-3 col-sm-3': props.jobOfferListCategory === Statuses.active
  })

  return (
    <div className='columns job-offer-card__counters-wrapper'>
      {
        props.counters.map((counter, id) => {
          return (
            <div key={id} className='col-sm-2 job-offer-card__wrapper-small'>
              <Link className={counterStyle} key={id} to={counter.link}>
                <div className='job-offer-card__counter'>{counter.amount}</div>
                <div
                  className='job-offer-card__label job-offer-card__label--under'>{counter.name}</div>
              </Link>
            </div>
          )
        })
      }
    </div>
  )
}

ApplicantsCounters.propTypes = {
  counters: PropTypes.arrayOf(
    PropTypes.shape({
      link: PropTypes.string.isRequired,
      amount: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired
    })
  )
}

export default ApplicantsCounters



