import React, { Component } from 'react'
import { isEmpty, omit, ifElse, equals, isNil, propOr } from 'ramda'
import PropTypes from 'prop-types'
import JobOfferValidator from 'Employer/validators/JobOfferValidator'
import { SmallWithIconButton, SmallJustIconButton } from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'
import ConfirmModal from 'Shared/components/DeleteModal/ConfirmModal'
import withTranslations from 'Shared/hoc/withTranslations'
import PublishJobOfferErrorConnector from 'Employer/connectors/PublishJobOfferError/PublishJobOfferErrorConnector'
import InactiveMemberPricingModal from 'Employer/components/InactiveMemberPricingModal'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { EmployerPermissions } from 'Shared/constants/User'

class DraftOfferButtons extends Component {
  static propTypes = {
    handlePost: PropTypes.func.isRequired,
    handleEdit: PropTypes.func.isRequired,
    handleDelete: PropTypes.func.isRequired,
    canUseJobSlots: PropTypes.bool.isRequired,
    canUseJobPosts: PropTypes.bool.isRequired,
    isCompanyProfileComplete: PropTypes.bool.isRequired
  }

  state = {
    showDeleteModal: false,
    showJobOfferNotCompleteModal: false,
    companyProfileCompletionModalVisible: false,
    isDemoAccountModalOpen: false
  }

  showCompanyProfileCompletionModalVisible = () => this.setState({ companyProfileCompletionModalVisible: true })
  hideCompanyProfileCompletionModalVisible = () => this.setState({ companyProfileCompletionModalVisible: false })

  openJobOfferNotCompleteModal = () => this.setState({ showJobOfferNotCompleteModal: true })
  closeJobOfferNotCompleteModal = () => this.setState({ showJobOfferNotCompleteModal: false })

  openDeleteModal = () => this.setState({ showDeleteModal: true })
  closeDeleteModal = () => this.setState({ showDeleteModal: false })

  openDemoAccountModal = () => this.setState({ isDemoAccountModalOpen: true })
  closeDemoAccountModal = () => this.setState({ isDemoAccountModalOpen: false })

  handleCompanyProfileModalConfirm = () => this.props.history.push('/employer/company-profile')

  handleJobPost = () => {
    const { isCompanyProfileComplete, data, neverHadAnyMembership } = this.props
    const validationWithoutSpecificFields = omit(['location', 'salary'], JobOfferValidator({ ...data, teamMembers: propOr([], 'members', data) }))
    const isLocationSet = !!data.googlePlacesId
    const isRemunerationSet = () => ifElse(
      equals('paid'),
      remType => !isNil(data.remuneration),
      remType => isNil(data.remuneration)
    )(data.remunerationType)

    const isValid = isEmpty(validationWithoutSpecificFields) && isLocationSet && isRemunerationSet()

    switch (true) {
      case neverHadAnyMembership:
        return this.openDemoAccountModal()
      case !isValid:
        return this.openJobOfferNotCompleteModal()
      case !isCompanyProfileComplete:
        return this.showCompanyProfileCompletionModalVisible()
      default:
        return this.props.handlePost()
    }
  }

  deleteJobOffer = async () => this.props.handleDelete(this.state.volunteeringRemoveId)

  render () {
    const { t, handleEdit } = this.props
    return (
      <div className='job-offer-card__draft-buttons'>
        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallWithIconButton>
            <i className='icon-im-post' />
            {t('common.post')}
          </SmallWithIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
          jobOfferId={this.props.data.id}
        >
          <SmallWithIconButton onClick={this.handleJobPost}>
            <i className='icon-im-post' />
            {t('common.post')}
          </SmallWithIconButton>
        </LimitedAccessWrapperConnector>

        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton><i className='ion-md-create' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
          jobOfferId={this.props.data.id}
        >
          <SmallJustIconButton onClick={handleEdit}><i className='ion-md-create' /></SmallJustIconButton>
        </LimitedAccessWrapperConnector>

        <LimitedAccessWrapperConnector
          placeholderComponent={<SmallJustIconButton><i className='ion-md-trash' /></SmallJustIconButton>}
          allowedFor={[EmployerPermissions.jobOfferId, EmployerPermissions.jobPosts]}
          jobOfferId={this.props.data.id}
        >
          <SmallJustIconButton onClick={this.openDeleteModal}><i className='ion-md-trash' /></SmallJustIconButton>
        </LimitedAccessWrapperConnector>
        <DeleteModal
          title={t('remove.jobOffer.title')}
          content={t('remove.jobOffer.description')}
          handleDelete={this.deleteJobOffer}
          isModalVisible={this.state.showDeleteModal}
          hideDeleteModal={this.closeDeleteModal}
        />
        <ConfirmModal
          title={t('modals.jobOfferNotComplete.title')}
          content={t('modals.jobOfferNotComplete.description')}
          handleConfirm={handleEdit}
          submitText={t('common.editJobOffer')}
          isModalVisible={this.state.showJobOfferNotCompleteModal}
          hideConfirmModal={this.closeJobOfferNotCompleteModal}
        />
        <ConfirmModal
          title={t('modals.companyProfileIncomplete.title')}
          content={t('modals.companyProfileIncomplete.descriptionShort')}
          handleConfirm={this.handleCompanyProfileModalConfirm}
          submitText={t('common.provideCompanyDetails')}
          isModalVisible={this.state.companyProfileCompletionModalVisible}
          hideConfirmModal={this.hideCompanyProfileCompletionModalVisible}
        />
        <PublishJobOfferErrorConnector />
        <InactiveMemberPricingModal
          show={this.state.isDemoAccountModalOpen}
          handleClose={this.closeDemoAccountModal}
        />
      </div>
    )
  }
}

export default withTranslations(DraftOfferButtons)



