import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobOffersView from 'Employer/views/JobOffersView'
import { createNewJob, fetchOrganisationJobOffersCounts } from 'Shared/ducks/jobOffers'
import { LIST_ITEMS_PER_PAGE } from 'Shared/constants/Global'

const mapStateToProps = state => ({
  count: state.jobOffers.count,
  perPage: LIST_ITEMS_PER_PAGE
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  createNewEmptyJob: async () => {
    const { id } = await dispatch(createNewJob({}))
    return ownProps.history.push(`/employer/job-offer/${id}/add`)
  },
  fetchOrganisationJobOffersCounts: () => dispatch(fetchOrganisationJobOffersCounts())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JobOffersView))



