import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import DraftOfferButtons from 'Shared/components/JobOfferListElement/components/DraftOfferButtons'
import { updateJobOfferById, deleteJobOfferById } from 'Shared/ducks/jobOffers'
import { Statuses } from 'Shared/constants/Job'
import { hasJobSlotsAvailable, hasJobPostsAvailable, neverHadAnyMembership } from 'Shared/selectors/membership'
import { getEmployerCompanyDetailsCompletion } from 'Shared/selectors/organisations'

const mapStateToProps = state => ({
  neverHadAnyMembership: neverHadAnyMembership(state),
  isCompanyProfileComplete: getEmployerCompanyDetailsCompletion(state),
  canUseJobSlots: hasJobSlotsAvailable(state),
  canUseJobPosts: hasJobPostsAvailable(state)
})

const mapDispatchToProps = (dispatch, ownProps) => {
  const { id } = ownProps.data

  return ({
    handlePost: () => dispatch(updateJobOfferById(id, { status: Statuses.active })),
    handleEdit: () => ownProps.history.push(`/employer/job-offer/${id}/edit`),
    handleDelete: () => dispatch(deleteJobOfferById(id))
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DraftOfferButtons))



